.skills__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.skills__container > div {
    background: var(--background_color_2);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.skills__container > div:hover {
    background: transparent;
    border-color: var(--primary_color_2);
    cursor: default;
}

.skills__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--primary_color_1);
}

.skills__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.skills__details {
    display: flex;
    gap: 1rem;
}

.skills__details-icon {
    margin-top: 6px;
    color: var(--primary_color_1);
}

/* ---------- Medium Devices ---------- */

@media screen and (max-width: 1024px) {
    .skills__container {
        grid-template-columns: 1fr;
    }

    .skills__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills__content {
        padding: 1rem;
    }
}

/* ---------- Small Devices ---------- */

@media screen and (max-width: 600px) {
    .skills__container {
        gap: 1rem;
    }

    .skills__container > div {
        width: 10;
        padding: 2rem 1rem;
    }

    .skills__details {
        gap: 0;
    }

    .skills__details-icon {
        min-height: 15px;
        min-width: 15px;
        max-height: 15px;
        max-width: 15px;
    }
}